import React, { useEffect, useState } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { Grid, Box } from '@material-ui/core';
import loginFrame from './icons/loginFrame.png'

const useStyles = makeStyles(theme => ({
  main_logo: {
    // height: 40
  },
  description: {
    fontSize: 48,
    color: '#272950',
    lineHeight: '60px',
    fontWeight: 500,
    maxWidth: 445,
    marginTop: 64
  }, 
  loginFrameImg: {
    width: '100%',
    height: '100vh !important',
    [theme.breakpoints.down('sm')]: {
      height: '60vh !important'
    },
  },
  container: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    '& .MuiBox-root-8': {
      minHeight: 'auto !important',
    },
    [theme.breakpoints.down('sm')]: {
      margin: "1.5rem 1rem",
    },
    [theme.breakpoints.down('xs')]: {
      "& $loginFormCard": {
        width: "100%",
      }
    },
  },
  purpleBg: {
    position: "absolute",
    left: "-35.08%",
    top: "-17.59%",
    bottom: "-51.05%",
    width: "1508.81px",
    height: "1427.69px",
    background: "linear-gradient(141.55deg, #848CF7 -2.89%, #F5C7F1 82.82%)",
    opacity: 0.4,
    filter: "blur(240px)",
    transform: "rotate(103.09deg)",
    zIndex: -10000
  },
  orangeBg: {
    position: "absolute",
    left: "-5%",
    bottom: "-8%",
    width: "420.95px",
    height: "440.47px",
    background: "linear-gradient(141.55deg, #ED7474 -3.79%, #FFE1BE 82.82%)",
    filter: "blur(200px)",
    transform: "rotate(-90deg)",
    zIndex: -1000
  }
}));

const Auth = props => {
  const { children } = props;

  const classes = useStyles();

  return (
    <Grid container>
      <Grid item xs={12} sm={12} md={6}>
        <Box style={{padding: 40, height: '100vh', position: "relative", overflow: "hidden"}}>
          <img src="/images/Logo.svg" className={classes.main_logo}/>
          <Box className={classes.description}>
            Let's get you set up with a Clutch Wallet profile!
          </Box>
          <Box className={classes.purpleBg}></Box>
          <Box className={classes.orangeBg}></Box>
        </Box>
      </Grid>
      <Grid item xs={12} sm={12} md={6} className={classes.container}>
        {children}
      </Grid>
    </Grid>
  );
};

export default Auth;
