import { Box, Button, CircularProgress, Dialog, Grid, Modal } from '@material-ui/core';
import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { setExpertMode } from 'store/actions/App';
import Typography from 'theme/typography';
import { FirstIcon, FirstTimerIcon, SecondIcon, SecondTimerIcon, ThirdIcon, FourthIcon, FifthIcon } from './icons/icon';
import useStyles from './style';

const ExpertModal = props => {
  const { open, handleClose } = props;
  const classes = useStyles();
  const [step, setStep] = useState(0);
  const [isExpert, setIsExpert] = useState(false);
  const dispatch = useDispatch();
  
  return (
    console.log(open),
    <Grid container spacing={1}> 
        <Grid item xs={12} sm={12} md={12}> 
          <Dialog onClose={handleClose} open={open} className={classes.dialog} disableBackdropClick>
            {
              step == 0 ? 
              <>
                <Box className={classes.header} style={{margin: '33px'}}>
                  Welcome to Clutch Wallet
                </Box>
                <FirstIcon style={{margin: 'auto'}}/>
                <Box className={classes.content_wrapper}>
                  <Box className={classes.content}>
                    Your crypto HQ that allows you to manage everything in one place - NFTs, crypto, and more. Continue reading to learn more.
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => setStep(1)}>
                    Next
                  </Button>
                </Box>
              </>
              :
              step == 1 ?
              <>
                <Box className={classes.header} style={{marginTop: '33px'}}>
                  Before you start
                </Box>
                <Box className={classes.subHeader}>
                  Are you:
                </Box>
                <Box style={{display: 'flex', justifyContent: 'center', marginBottom: 52}}>
                  <Box style={{marginRight: 35}} onClick={() => setIsExpert(false)}>
                    <Box className={!isExpert ? classes.selectedImageBox : classes.imageBox}>
                      <FirstTimerIcon />
                    </Box>
                    <Box className={!isExpert ? classes.selectedTextBox : classes.textBox}> First Timer</Box>
                  </Box>
                  <Box onClick={() => setIsExpert(true)}>
                    <Box className={!isExpert ? classes.imageBox : classes.selectedImageBox}>
                      <SecondTimerIcon />
                    </Box>
                    <Box className={!isExpert ? classes.textBox : classes.selectedTextBox}> Expert </Box>
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => setStep(isExpert ? 2 : 3)}>
                    Let's Start!
                  </Button>
                </Box>
              </>
              :
              step == 2 ?
              <>
                <Box className={classes.header} style={{margin: '33px'}}>
                  Enjoy!
                </Box>
                <SecondIcon style={{margin: 'auto'}}/>
                <Box className={classes.content_wrapper}>
                  <Box className={classes.content}>
                    Connect to an existing wallet or use your own Clutch Wallet to store,  transact and understand crypto.
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => {
                    dispatch(setExpertMode(true)); localStorage.setItem('expert', 'true');
                  }}>
                    Finish
                  </Button>
                </Box>
              </>
              :
              step == 3 ?
              <>
                <Box className={classes.header} style={{margin: '33px'}}>
                  The future of finance
                </Box>
                <ThirdIcon style={{margin: 'auto'}}/>
                <Box className={classes.content_wrapper}>
                  <Box className={classes.content}>
                  Clutch wants to help you store,  transact and understand crypto. To start, we’ve created a wallet for you - in the application, select the top right button to access your wallet address.
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => setStep(4)}>
                    Next
                  </Button>
                </Box>
              </>
              :
              step == 4 ?
              <>
                <Box className={classes.header} style={{margin: '33px'}}>
                  Transact with ease
                </Box>
                <FourthIcon style={{margin: 'auto'}}/>
                <Box className={classes.content_wrapper}>
                  <Box className={classes.content}>
                    Swap, send and receive your favorite crypto assets.
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => setStep(5)}>
                    Next
                  </Button>
                </Box>
              </>
              :
              step == 5 ?
              <>
                <Box className={classes.header} style={{margin: '33px'}}>
                  Learn
                </Box>
                <FifthIcon style={{margin: 'auto'}}/>
                <Box className={classes.content_wrapper}>
                  <Box className={classes.content}>
                    Explore our education guides and catch up on the latest news from top crypto media outlets.
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => setStep(6)}>
                    Next
                  </Button>
                </Box>
              </>
              :
              <>
                <Box className={classes.header} style={{margin: '33px'}}>
                  That's it!
                </Box>
                <SecondIcon style={{margin: 'auto'}}/>
                <Box className={classes.content_wrapper}>
                  <Box className={classes.content}>
                   To learn more about how to use our products, please explore our guides.
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => {
                    dispatch(setExpertMode(true)); localStorage.setItem('expert', 'true');
                  }}>
                    Let's Start
                  </Button>
                </Box>
              </>
            }
          </Dialog>
        </Grid>
    </Grid>
    
  );
};

export default ExpertModal;
